*{
    padding: 0;
    margin: 0;
    font-family: "Rubik Medium";
    font-size: 50px;
}

#header-shirts{
    margin: 10px;
    text-align: center;
}

#field-container{
    width: 100%;
    display: flex;
    margin-top: 40px;
    justify-content: center;
}

#board{
    width: 600px;
    height: 600px;
    padding: 20px;
    background-color: bisque;
    display: grid;
}

.card{
    width: 110px;
    height: 120px;
    background-color: gray;
    margin: auto auto;
}

.shirt{
    width: 150px;
    height: 150px;
}

#shirtList{
    display: flex;
    justify-content: center;
}

#shirtList *{
    margin: 0 100px;
}

#settings-pane{
    background-color: #dadada;
    padding-top: 120px;
    margin-left: 30px;
    width: 320px;
}

#settings-pane *{
    font-size: 40px;
}

#settings-pane div,
#settings-pane h1{
    text-align: center;
    margin: 10px 0;
}

#newGameBtn{
    width: 80%;
}

#win-counter{
    font-size: 60px;
    color: #46e62a;
}